___CSS_LOADER_EXPORT___.push([module.id, ".badge[data-v-7b2de43e]{font-size:.7rem;margin-left:2px}", "",{"version":3,"sources":["webpack://./resources/js/components/UserSettings/Roles/Roles.vue"],"names":[],"mappings":"AACA,wBACA,eAAA,CACA,eAEA","sourcesContent":["<style scoped>\n.badge{\n\tfont-size: 0.7rem;\n\tmargin-left: 2px;\n\t\n}\n</style>\n\n<style src=\"vue-multiselect/dist/vue-multiselect.min.css\"></style>\n\n<template>\n\t<div>\n\t\t<div class=\"card-body\">\n\t\t\t<div class=\"form-inline ml-2\">\n\t\t\t\t<button class=\"btn btn-success btn-sm mr-2\" @click=\"showCreateRoleModal\" v-if=\"$can('user_create')\">\n\t\t\t\t\t<i class=\"fas fa-plus-circle\"></i><span><b> Role</b></span>\n\t\t\t\t</button>\n\n                <button class=\"btn btn-secondary btn-sm mr-5\" @click=\"getRoles\">\n\t\t\t\t\t<i class=\"fas fa-sync-alt\"></i>\n\t\t\t\t</button>\n\n                <select @change=\"getRoles\" class=\"custom-select my-1 mr-sm-2\" id=\"inlineFormCustomSelectPref\">\n\t\t\t\t\t<option value='-1' disabled>Worker...</option>\n\t\t\t\t\t<!-- <option v-for='user in users' :value='user.id' :key=\"user.id\">{{ user.full_name }}</option> -->\n                </select>                \n            </div>\n\t\t\t<div class=\"row mt-3\">\n\t\t\t\t<table class=\"border table table-hover\">\n\t\t\t\t\t<thead>\n\t\t\t\t\t\t<tr>\n\t\t\t\t\t\t\t<th>Name</th>\n\t\t\t\t\t\t\t<th>Permissions</th>\n\t\t\t\t\t\t\t<th>Actions</th>\n\t\t\t\t\t\t</tr>\n\t\t\t\t\t</thead>\n\t\t\t\t\t<tbody v-if=\"roles\">\n\t\t\t\t\t\t\t<tr v-for=\"role in roles\" :key=\"role.id\">\n\t\t\t\t\t\t\t\t<td>{{ role.name }}</td>\n\t\t\t\t\t\t\t\t<td class=\"row\">\n\t\t\t\t\t\t\t\t\t<span v-for=\"permission in role.permissions\" :key=\"permission.id\" class=\"badge badge-pill badge-primary\">\n\t\t\t\t\t\t\t\t\t\t{{permission.name}}\n\t\t\t\t\t\t\t\t\t</span>\n\t\t\t\t\t\t\t\t</td>\n\t\t\t\t\t\t\t\t<td>\n\t\t\t\t\t\t\t\t\t<a href=\"#\" @click=\"showEditRoleModal(role)\" v-if=\"$can('role_edit')\">\n\t\t\t\t\t\t\t\t\t\t<i class=\"fa fa-edit blue\"></i>\n\t\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t\t\t\n\t\t\t\t\t\t\t\t\t<a href=\"#\" @click=\"deleteRole(role.id)\" v-if=\"$can('role_delete')\">\n\t\t\t\t\t\t\t\t\t\t<i class=\"fa fa-trash red\"></i>\n\t\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t\t</td>\n\t\t\t\t\t\t\t</tr>\n\t\t\t\t\t</tbody>\n\t\t\t\t</table>\n\t\t\t</div>\n\t\t</div>\n\n\t\t<!-- Modal -->\n\t\t<div class=\"modal fade\" id=\"roleModal\" tabindex=\"-1\" aria-labelledby=\"roleModalLabel\" aria-hidden=\"true\">\n\t\t\t<div class=\"modal-dialog modal-dialog-centered\">\n\t\t\t\t<div class=\"modal-content\">\n\t\t\t\t\t<div class=\"modal-header\">\n\t\t\t\t\t\t<h5 v-show=\"!editMode\" class=\"modal-title\" id=\"roleModalLabel\">Create Role</h5>\n\t\t\t\t\t\t<h5 v-show=\"editMode\" class=\"modal-title\" id=\"roleModalLabel\">Edit Role</h5>\n\t\t\t\t\t\t<button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-label=\"Close\">\n\t\t\t\t\t\t\t<span aria-hidden=\"true\">&times;</span>\n\t\t\t\t\t\t</button>\n\t\t\t\t\t</div>\n\t\t\t\t\t<form @submit.prevent=\"editMode ? updateRole() : createRole()\">\n\t\t\t\t\t\t<div class=\"modal-body\">\n\t\t\t\t\t\t\t<div class=\"form-group\">\n\t\t\t\t\t\t\t\t<label for=\"name\" class=\"form-label\">Name</label>\n\t\t\t\t\t\t\t\t<input id=\"name\" v-model=\"form.name\" type=\"text\" name=\"name\" class=\"form-control\">\n\t\t\t\t\t\t\t\t<HasError :form=\"form\" field=\"name\" />\n\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\n\t\t\t\t\t\t\t<div class=\"form-group\" v-if=\"roles\">\n\t\t\t\t\t\t\t\t<label class=\"typo__label\">Permissions</label>\n\t\t\t\t\t\t\t\t<multiselect \n\t\t\t\t\t\t\t\tv-model=\"form.permissions\" \n\t\t\t\t\t\t\t\t:options=\"permissions\" \n\t\t\t\t\t\t\t\t:multiple=\"true\" \n\t\t\t\t\t\t\t\t:close-on-select=\"false\" \n\t\t\t\t\t\t\t\t:clear-on-select=\"false\" \n\t\t\t\t\t\t\t\t:preserve-search=\"true\" \n\t\t\t\t\t\t\t\tplaceholder=\"Pick some\" \n\t\t\t\t\t\t\t\tlabel=\"name\" \n\t\t\t\t\t\t\t\ttrack-by=\"name\" \n\t\t\t\t\t\t\t\t:preselect-first=\"true\">\n\t\t\t\t\t\t\t\t\t<!-- <template slot=\"selection\" slot-scope=\"{ values, search, isOpen }\"><span class=\"multiselect__single\" v-if=\"values.length &amp;&amp; !isOpen\">{{ values.length }} options selected</span></template> -->\n\t\t\t\t\t\t\t\t</multiselect>\n\t\t\t\t\t\t\t\t<!-- <pre class=\"language-json\"><code>{{ value  }}</code></pre> -->\n\t\t\t\t\t\t\t</div>\n\n\t\t\t\t\t\t</div>\n\t\t\t\t\t\t<div class=\"modal-footer\">\n\t\t\t\t\t\t\t<button type=\"button\" class=\"btn btn-danger\" data-dismiss=\"modal\">Close</button>\n\t\t\t\t\t\t\t<button v-show=\"!editMode\" type=\"submit\" class=\"btn btn-success\">Create</button>\n\t\t\t\t\t\t\t<button v-show=\"editMode\" type=\"submit\" class=\"btn btn-primary\">Update</button>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</form>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t</div>\n\t</div>\n</template>\n<script>\nimport Form from 'vform';\nimport Multiselect from 'vue-multiselect'\nimport {mapActions, mapGetters} from 'vuex'\nimport axiosMixin from '../../../mixins/axiosMixin'\n\nexport default {\n\tmixins: [axiosMixin],\n\tcomponents: { Multiselect },\n\tdata() {\n\t\treturn {\n\t\t\teditMode: false,\n\t\t\troles: {},\n\t\t\tpermissions: [],\n\t\t\tform: new Form({\n\t\t\t\tid: '',\n\t\t\t\tname: '',\n\t\t\t\tpermissions: [],\n\t\t\t})\n\t\t}\n\t},\n\tmethods: {\n\n\t\tshowCreateRoleModal(){\n\t\t\tthis.editMode = false;\n\t\t\t// this.form.reset()\n\t\t\t$('#roleModal').modal('show')\n\t\t},\n\t\tcreateRole() {\n\t\t\tthis.$Progress.start();\n\t\t\tthis.form.post('/api/roles')\n\t\t\t.then(() => {\n\t\t\t\t// success\n\t\t\t\tFire.$emit('rolesChanged');\n\n\t\t\t\t$('#roleModal').modal('hide')\n\t\t\t\tToast.fire({\n\t\t\t\t\ticon: 'success',\n\t\t\t\t\ttitle: 'Added successfully'\n\t\t\t\t})\n\t\t\t\t\n\t\t\t\tthis.$Progress.finish();\n\t\t\t})\n\t\t\t.catch(() => {\n\t\t\t\t// error\n\t\t\t\tthis.$Progress.fail();\n\t\t\t})\n\t\t},\n\n\t\tshowEditRoleModal(role){\n\t\t\tthis.editMode = true;\n\t\t\tthis.form.reset()\n\t\t\t$('#roleModal').modal('show')\n\t\t\tthis.form.fill(role)\n\t\t},\n\t\tupdateRole(){\n\t\t\tthis.$Progress.start();\n\t\t\tthis.form.put('/api/roles/'+this.form.id)\n\t\t\t.then(() => {\n\t\t\t\t// success\n\t\t\t\tFire.$emit('rolesChanged');\n\t\t\t\t$('#roleModal').modal('hide')\n\t\t\t\tSwal.fire(\n\t\t\t\t\t'Updated!',\n\t\t\t\t\t'It has been updated.',\n\t\t\t\t\t'success'\n\t\t\t\t)\n\t\t\t\tthis.$Progress.finish();\n\t\t\t})\n\t\t\t.catch(() => {\n\t\t\t\t// error\n\t\t\t\tthis.$Progress.fail();\n\t\t\t})\n\t\t},\n\n\t\tdeleteRole(id){\n\t\t\tSwal.fire({\n\t\t\t\ttitle: 'Are you sure?',\n\t\t\t\ttext: \"You won't be able to revert this!\",\n\t\t\t\ticon: 'warning',\n\t\t\t\tshowCancelButton: true,\n\t\t\t\tconfirmButtonColor: '#3085d6',\n\t\t\t\tcancelButtonColor: '#d33',\n\t\t\t\tconfirmButtonText: 'Yes, delete it!'\n\t\t\t})\n\t\t\t.then((result) => {\n\t\t\t\tif (result.isConfirmed) {\n\t\t\t\t\tthis.$Progress.start();\n\t\t\t\t\tthis.form.delete('/api/roles/'+id)\n\t\t\t\t\t.then(() => {\n\t\t\t\t\t\t// success\n\t\t\t\t\t\tFire.$emit('rolesChanged');\n\t\t\t\t\t\tSwal.fire(\n\t\t\t\t\t\t\t'Deleted!',\n\t\t\t\t\t\t\t'It has been deleted.',\n\t\t\t\t\t\t\t'success'\n\t\t\t\t\t\t)\n\t\t\t\t\t\tthis.$Progress.finish();\n\t\t\t\t\t})\n\t\t\t\t\t.catch(() => {\n\t\t\t\t\t\t// error\n\t\t\t\t\t\tthis.$Progress.fail();\n\t\t\t\t\t\tToast.fire({\n\t\t\t\t\t\t\ticon: 'error',\n\t\t\t\t\t\t\ttitle: 'There is an error!'\n\t\t\t\t\t\t})\n\t\t\t\t\t});\n\t\t\t\t}\n\t\t\t})\n\t\t},\n\n\t},\n\t\n\tcreated() {\n\t\tthis.getRoles();\n\t\tthis.getPermissions();\n\n\t\tFire.$on('rolesChanged', () => {\n\t\t\tthis.getRoles();\n\t\t});\n\n\t\t\n\t}\n}\n</script>"],"sourceRoot":""}]);
